import React, { useState, useEffect } from 'react';
import testCont from './testcontent.js';

const ArticlePage = ({match}) => {
    // fetch('/api/articles/...',{
    //     method: 'POST',
    //
    // });
const name = match.params.name;
const article = testCont.find(article => article.name === name);

const [articleInfo, setArticleInfo] = useState({ upvotes: 0}) ;
useEffect(() => {
    const fetchData = async () => {
        const result = await fetch(`/api/articles/${name}`);
        const body = result.json();
        console.log("body", body);
    setArticleInfo(body);

}
    fetchData();
//  setArticleInfo({ upvotes: Math.ceil(Math.random() * 10)});
}, [name]);

if(!article) return <h1> no matching test </h1>;
    return (
       <>
       <h1> this is a {article.name} page</h1>
        <p> this page has been upvoted {articleInfo.upvotes} </p>
       <p> this is a {article.title} title</p>
       </>
    );
};

export default ArticlePage;