import Homepage from './pages/homepage.js';
import ArticlePage from './pages/test.js';
import NavBar from './navbar.js';

import {
    BrowserRouter as Router,
    Route,
} from 'react-router-dom';

import './App.css';

function App() {
  return (
      <Router>
    <div className="App">
        <NavBar />
        <Route path="/" component={Homepage} exact />
      <Route path="/test/:name" component={ArticlePage} />
    </div>
    </Router>
  );
}

export default App;
